<script>
  export let text = "Process Engineering";
  export let type = 1;
  export let colorNumber = 1;

  let colors = [
    "bg-green-400",
    "bg-brand-orange",
    "bg-brand-weak-blue",
    "bg-brand-strong-blue",
    "bg-brand-weak-yellow",
    "bg-brand-strong-yellow",
    "bg-brand-weak-red",
    "bg-brand-strong-red",
    "bg-brand-weak-green",
    "bg-brand-strong-green",
  ];

  colorNumber =
    colorNumber >= colors.length
      ? 1 + Math.floor(Math.random() * colors.length - 1)
      : colorNumber;
</script>

<div
  class={`rounded-lg shadow-md overflow-hidden relative text-center flex justify-center items-center h-12 md:h-16 px-2`}
>
  <div
    class={`${colors[colorNumber]} absolute top-0 right-0 bottom-0 left-0 z-0`}
    style="opacity:.2"
  />
  {#if type == 1}
    <img
      src="./images/blank-circle.svg"
      class="absolute left-0 top-0"
      style="opacity:.2;transform:translate(-50%,-50%)"
      alt="blank circle"
    />
  {:else}
    <img
      src="./images/blank-square.svg"
      class="absolute left-0 top-0"
      style="opacity:.2;transform:translate(-50%,-50%)"
      alt="blank square"
    />
  {/if}
  <h1 class="relative z-10 rubik">{text}</h1>
</div>
