<script>
  import { link } from "svelte-spa-router";
  export let title = "";
  export let date = "";
  export let excerpt = "";
  export let slug = "";
  export let withReload = false;
</script>

<div class="px-3 py-4 md:p-8 mb-4 sm:mb-0 rounded-md bg-gray-800 shadow-lg">
  <h1 class="rubik mb-5 text-lg md:text-xl flex items-center font-semibold">
    {title}
  </h1>

  <h1 class="mb-5 text-sm">{excerpt}...</h1>

  <div class="flex justify-between items-center">
    <h2
      class="mb-1 uppercase text-sm tracking-wider text-brand-strong-blue flex
      items-center"
    >
      <i class="fas fa-clock mr-3" />
      {date}
    </h2>
    {#if !withReload}
      <a
        use:link
        href={`/blog/${slug}`}
        class="text-sm uppercase tracking-wider text-brand-strong-green flex
         items-center"
      >
        View Post
        <i class="fas fa-arrow-right ml-3" />
      </a>
    {:else}
      <button
        on:click={() => {
          window.location = `/#/blog/${slug}`;
          window.location.reload();
        }}
        class="text-sm uppercase tracking-wider text-brand-strong-green flex
      items-center"
      >
        View Post
        <i class="fas fa-arrow-right ml-3" />
      </button>
    {/if}
  </div>
</div>
