<script>
  export let position = "";
  export let location = "";
  export let period = "";
  export let university = "";
  export let activities = [];
</script>

<div
  class="p-4 md:p-8 shadow-md bg-gray-800 rounded-lg relative overflow-hidden"
>
  <h1 class="mb-1 text-xl flex items-center">
    <img
      src="./images/cyclones-print-icon.svg"
      class="h-12 w-12 mr-4"
      alt="cyclones print"
    />
    {position}
  </h1>
  <h2 class="mb-1 uppercase text-sm tracking-wide text-brand-strong-blue">
    {period}
  </h2>
  <h1 class="mb-1 text-brand-strong-green">
    <i class="fas fa-location-arrow mr-3" />
    {location}
  </h1>
  <h1 class="mb-4 text-lg text-brand-orange leading-none">{university}</h1>
  <p class="mb-2 text-sm">
    {#each activities as item, i (i)}
      <div class="mb-4 text-gray-400">• {item}</div>
    {/each}
  </p>

  <img
    class="absolute top-0 left-0"
    style="transform:translate(25%,25%);opacity:.75;z-index:0;"
    src="./images/cyclones-print.svg"
    alt=""
  />
</div>
