<script>
  export let media = "";
  export let title = "";
  export let date = "August 2021";
  export let url = "";
  export let lang = "English";
</script>

<div class="p-4 md:p-6 mb-4 sm:mb-0 rounded-md bg-brand-darker-black shadow-sm">
  <h1 class="mb-5 text-lg md:text-xl flex items-center text-green-400">
    <img src="./images/mic.svg" class="h-8 w-8 mr-2" alt="cyclones print" />
    {media}
  </h1>

  <h1 class="mb-5 text-sm flex items-center">
    <div
      class="py-1 px-3 rounded-full bg-green-200 text-gray-800 mr-3 text-xs uppercase rubik font-semibold"
    >
      {lang}
    </div>
    {title}
  </h1>

  <div class="flex justify-between items-center">
    <h2
      class="mb-1 uppercase text-sm tracking-wider text-gray-500 flex
    items-center"
    >
      <i class="fas fa-clock mr-2" />
      {date}
    </h2>
    <a
      class="text-sm uppercase tracking-wider text-yellow-400 flex
      items-center"
      target="_blank"
      href={url}
    >
      see interview
      <i class="fas fa-microphone ml-3" />
    </a>
  </div>
</div>
