<script>
  import SectionHeading from "../shared/SectionHeading.svelte";
  import axios from "axios";
  import Container from "../shared/Container.svelte";
  import Grid from "../shared/Grid.svelte";

  let sending = false;

  let message = {
    name: "",
    email: "",
    tel: "",
    object: "",
    content: "",
  };

  let handleSubmit = () => {
    sending = true;
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${process.env.SEND_GRID_KEY}`,
    };
    var data = {
      personalizations: [
        {
          to: [
            {
              email: "kapiambaf@gmail.com",
            },
          ],
        },
      ],
      from: {
        email: "glorykat777@gmail.com",
      },
      subject: `🔔 PORTFOLIO ALERT 🔔 From ${message.name} on The Subject ${message.object}`,
      content: [
        {
          type: "text/plain",
          value: `
Name : ${message.name}\n
Telephone : ${message.tel}\n
Email : ${message.email}\n
Subject : ${message.object}\n
------------------\n\n\n
${message.content}
          `,
        },
      ],
    };
    axios
      .post(
        "https://cors-anywhere.herokuapp.com/https://api.sendgrid.com/v3/mail/send",
        data,
        { headers }
      )
      .then((response) => {
        sending = false;
        alert(
          `Thank You For the Message ${message.name}, I'll get back to you`
        );
        message.content = "";
        message.email = "";
        message.name = "";
        message.object = "";
        message.tel = "";
      })
      .catch((error) => {
        sending = false;
        alert(
          `Sorry, There was an unexpected Error 😞, In a sharing mood? Please report it to katendeglory@gmail.com, I'll fix it so others won't have the same issue`
        );
        console.log(error);
      });
  };
</script>

<div class="relative" id="contact">

  <Container>

    <SectionHeading text1="CONTACT" text2="ME" breakWord />

    <h1 class="text-lg mt-20 text-center">
      To get in touch with me, use my contact details provided below, or fill
      out the form to leave your message.
    </h1>

    <div
      class="mt-16 pt-4 pl-4 w-full shadow-md bg-brand-darker-black rounded-lg
      border-l-2 border-brand-strong-green">
      <Grid sm>
        <div class="flex items-center mb-4">
          <i
            class="text-brand-weak-green fas fa-phone-square text-brand-green
            text-3xl mr-3" />
          <a href="tel:+14192808399">+1 419-280-8399</a>
        </div>
        <div class="flex items-center mb-4">
          <i
            class="text-brand-weak-green fab fa-whatsapp text-brand-green
            text-3xl mr-3" />
          <a href="https://wa.me/+14192808399" target="_blank">
            +1 419-280-8399
          </a>
        </div>
        <div class="flex items-center mb-4">
          <i
            class="text-brand-weak-green fas fa-envelope-square text-brand-green
            text-3xl mr-3" />
          <a href="mailto: kapiambaf@gmail.com" target="_blank">
            kapiambaf@gmail.com
          </a>
        </div>
        <div class="flex items-center mb-4">
          <i
            class="text-brand-weak-green fab fa-linkedin text-brand-green
            text-3xl mr-3" />
          <a
            href="https://www.linkedin.com/in/kapiamba-fabrice/"
            target="_blank">
            Fabrice Kapiamba
          </a>
        </div>
      </Grid>
    </div>

    <div class="flex flex-col lg:flex-row mt-12">
      <div class="mb-10 lg:mt-0 lg:block w-full">
        <div class="mx-auto w-11/12 lg:w-10/12">
          <img src="./images/telephone.svg" alt="Telephone" />
        </div>
      </div>

      <form on:submit|preventDefault={handleSubmit} class="w-full">
        <div class="lg:flex">
          <input
            bind:value={message.name}
            type="text"
            class="text-field w-full lg:w-1/2 lg:mr-4 mb-4"
            required
            placeholder="Full name" />
          <input
            bind:value={message.tel}
            type="tel"
            class="text-field w-full lg:w-1/2 mb-4"
            required
            placeholder="Telephone number" />
        </div>

        <div class="lg:flex">
          <input
            bind:value={message.email}
            type="email"
            class="text-field w-full lg:w-1/2 lg:mr-4 mb-4"
            required
            placeholder="Email Address" />
          <input
            bind:value={message.object}
            type="text"
            class="text-field w-full lg:w-1/2 mb-4"
            required
            placeholder="Object" />
        </div>

        <div class="">
          <textarea
            bind:value={message.content}
            class="text-field w-full mb-4"
            required
            rows="6"
            placeholder="Type your message here..." />
        </div>

        <div class="w-full flex justify-end">
          <button
            disabled={sending}
            class="text-field text-brand-strong-green flex items-center text-lg
            w-full">
            {#if sending}
              Sending...
              <i class="ml-3 fas fa-circle-notch animate-spin fa-lg" />
            {:else}
              Send Your Message
              <i class="ml-3 fas fa-envelope fa-lg" />
            {/if}
          </button>
        </div>
      </form>

    </div>

  </Container>

</div>
