<script>
  export let text1 = "";
  export let text2 = "";
  export let colorClass = "text-brand-strong-green";
  export let breakWord = false;
  export let sm = false;
</script>

<div
  class={`rubik text-center pt-20 mb-10 ${
    sm ? "text-4xl" : "text-5xl"
  } md:text-5xl lg:text-6xl justify-center flex ${
    breakWord && "flex-col md:flex-row"
  }
  `}
>
  <h1 class="mr-3">{text1}</h1>
  <h1 class={colorClass}>{text2}</h1>
</div>
